import Vue from 'vue'
import Router from 'vue-router'
import Layout from '@/pages/layout'
import VueLess from '@/components/VueLess'

Vue.use(Router)


// 这里是路由的脚本文件,用于配置路由

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Layout',
      component: Layout
    }
  ]
})



