<!--模板-->
<template>
    <div id="header">
        <div class="box">
            <img src="../assets/logo.png" alt="">
            <div class="text">尚学堂旗下品牌,尚学堂旗下品牌,尚学堂旗下品牌</div>
            <div class="topRight">
                <a href="">登录</a>
                <span>|</span>
                <a href="">注册</a>
                <span>|</span>
                <a href="">关于</a>
            </div>
        </div>
    </div>
</template>

<!--行为-->
<script>
    export default {
        name: 'headers',
        data () {
            return {}
        }
    }
</script>

<!--样式-->
<style scoped>
    .box {
        width: 100%;
        height: 100px;
        background: #8f8b8d;
    }
    .box img{
        width:50px;
        padding:25px  10px 0 50px;
        float: left;
    }
    .box .text{
        width:900px;
        line-height:100px;
        font-size:20px;
        color: #fff;
    }
    .topRight{
        line-height:100px;
        position: absolute;
        top:0;
        right:0;
        margin-right:100px;
        color: #fff;
    }

    .topRight span{
        margin:0 10px;
    }
    .topRight a{
        color: #fff;
    }
    a:hover{
        color: #4cb18e;
    }

</style>


