<!--模板(Vue是MVVM类型的库 界面使用的是数据绑定基础架构模型,而不是根据视图界面驱动)-->
<template>
    <div id="content">

        <!--left 左侧菜单-->
        <div class="left-list">
            <!--左侧菜单上半部分-->
            <h2 class="list-top-title" >全部产品</h2>
            <!--渲染productList数组,总共2个类型,参数为标题 -->
            <div class="list-top" v-for="(item , index) in productList">
                <h3>{{ item.category }}</h3>
                <ul class="list-content">
                    <!--渲染列表中的点击按钮元素/并判断hot值 列表本身是存在于productList中的一个数组-->
                    <li  v-for="ls in item.list">
                        <a :href="ls.url" target="_blank">{{ ls.title }}</a>
                <!--HOT元素存在与否取决于 li 元素中的hot为true或者false-->
                        <span v-if="ls.hot" class="hotTag">HOT</span>
                    </li>
                </ul>
                <hr v-show="index%2 == 0" class="list-line">
            </div>

            <!--左侧菜单下半部分-->
            <div class="list-bottom">
                <h2 class="list-top-title" >最新消息</h2>
                    <ul class="list-content" style="padding:10px 30px;">
                        <li  v-for="item in news">
                            <a href="https://www.baidu.com" target="_blank">{{ item.username }}</a>
                        </li>
                    </ul>
            </div>
        </div>




        <div class="right-content">
            <!--轮播图-->
            <swiper :options="swiperOption" ref="mySwiper" class="swipers">
                <!-- slides -->
                <swiper-slide>
                    <a href="http://www.taobao.com" target="_blank">
                        <img src="http://img.zcool.cn/community/01df2a566b88b86ac7256b298a3fcf.jpg" alt="" style=" width:100%;height:100%;">
                    </a>
                </swiper-slide>
                <swiper-slide>
                    <a  href="http://www.taobao.com" target="_blank">
                        <img src="http://img.zcool.cn/community/01511c57f9fc0ea84a0d304f5dc0c0.jpg" alt="" style=" width:100%;height:100%;">
                    </a>

                </swiper-slide>
                <swiper-slide>

                    <a  href="http://www.taobao.com" target="_blank">
                    <img src="http://img.zcool.cn/community/01afd35799b5850000012e7ec25a52.jpg" alt="" style=" width:100%;height:100%;">
                    </a>
                </swiper-slide>
                <swiper-slide>
                    <a  href="http://www.taobao.com" target="_blank">
                        <img src="http://img.zcool.cn/community/01d29c584677a7a8012060c8972431.jpg" alt="" style=" width:100%;height:100%;">
                    </a>
                </swiper-slide>


                <!-- Optional controls -->
                <div class="swiper-pagination"  slot="pagination"></div>
                <div class="swiper-button-prev" slot="button-prev"></div>
                <div class="swiper-button-next" slot="button-next"></div>

            </swiper>

            <!--选项-->
            <div class="index-board-list">
                <!--绑定两个类名
                    1 : 绑定 index-board 加上 选项盒子的标识符
                    2 : 绑定 索引模于2 不等于0的元素左侧浮动15px(偶数索引)
                -->
                <div class="index-board-item" v-for="(item,index) in boradList" :class="['index-board-'+item.tag,{'index-board-item-evenNumber':index%2!==0}]">
                    <h2>{{item.title}}</h2>
                    <p>{{item.desc}}</p>
                    <div class="list-button">
                        <a href="http://www.taobao.com" target="_blank">立即购买</a>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<!--行为-->
<script>
    export default {
        name: 'layout',
        data () {
            return {
                // swiper 轮播图配置
                swiperOption:{
                    //添加分页器
                    pagination: {
                        el: '.swiper-pagination',
                        clickable :true
                    },
                    loop:true,
                    navigation: {
                        nextEl: '.swiper-button-next',
                        prevEl: '.swiper-button-prev',
                    },
                    autoplay: {
                        delay: 3000,
                        stopOnLastSlide: false,
                        disableOnInteraction: true,
                    },

                },
                //一共有2个列表,所以将数据写成数组
                productList:[
                    {
                        category:"手机应用类",
                        list:[
                            {
                                title:"91助手",
                                url:"http://zs.91.com/",
                                hot:false
                            },
                            {
                                title:"豌豆荚",
                                url:"https://www.wandoujia.com/",
                                hot:true
                            },
                            {
                                title:"金山毒霸",
                                url:"http://www.ijinshan.com/",
                                hot:false
                            }
                        ]
                    },
                    {
                        category:"开发工具",
                        list:[
                            {
                                title:"webstorm",
                                url:"http://www.jetbrains.com/webstorm/",
                                hot:false
                            },
                            {
                                title:"sublime",
                                url:"https://www.sublimetext.com/",
                                hot:true
                            },
                            {
                                title:"Hbuilder",
                                url:"http://www.dcloud.io/",
                                hot:false
                            },
                            {
                                title:"atom",
                                url:"https://atom.io/",
                                hot:false
                            }
                        ]
                    }
                ],

                //新闻选项列表
                news:[],

                //选项
                boradList:[
                    {
                        //喇叭
                        title:"开放产品",
                        desc:"开放产品是一款开放的产品",
                        tag:"earth"
                    },
                    {
                        //地球
                        title:"品牌营销",
                        desc:"品牌营销帮助您找到更好的定位",
                        tag:"loud"
                    },
                    {
                        //小货车
                        title:"使命必达",
                        desc:"使命必达快速迭代永远保持最前端的速度",
                        tag:"car"
                    },
                    {
                        //山峰
                       title:"永攀高峰",
                        desc:"祝您永攀高峰,达到事业顶峰",
                        tag:"hill"
                    }
                ]
            }
        },
        created(){
            //这里使用ES5的写法需要在外部定义一下this,不然内部找不到,使用ES6的箭头函数则不需要
            var ts = this;
            this.$axios.get("http://jsonplaceholder.typicode.com/users",
                {
                    params:{
                        count:10,
                        type:"top"
                    }
                }
            ).then(function (response) {
                ts.news = response.data;
            }).catch(function (error) {
                console.log(error);
            })

            //这里使用ES6语法
            this.$axios.get("http://jsonplaceholder.typicode.com/photos",
            ).then(res => {
                this.imgs = res.data;
//                console.log(res.data);
            }).catch(error => {
                console.log(error);
            })
        }
    }
</script>

<!--样式-->
<style scoped>
    #content{
        width:1400px;
        margin:0 auto;
        padding: 15px 0;
        overflow: hidden;
        user-select: none;
    }

    /*左侧最外层*/
    .left-list{
        width:240px;
        float: left;
    }


    /*左侧顶部*/
    .list-top{
        width:100%;
        background: #fff;
        box-sizing: border-box;
        padding-bottom: 15px;
    }
    .list-top-title{
        width:100%;
        height: 35px;
        font-size: 16px;
        line-height:35px;
        color: #f6f7f9;
        background: #4cb18e;
        padding-left:15px;
        box-sizing: border-box;
    }
    .list-content{
        padding-left:30px;
        line-height:25px;
    }
    .left-list h3{
        font-weight:800;
        font-size:14px;
        padding: 10px 0;
        margin-left:15px;
    }
    .list-content a{
        color: #6e6e6e;
    }
    .list-content .hotTag{
        background: #ff0f10;
        color: #fff;
        font-size:12px;
    }
    .sw{
        width:100%;
        height:100%;
    }

    .list-line{
        margin-top:15px;
    }
    
    /*左侧尾部*/
    .list-bottom{
        width:100%;
        margin-top:15px;
        background: #fff;
    }


    /*右侧最外层*/
    .right-content{
        width:1145px;
        float: right;
    }
    .swipers{
        height:300px;
    }
    .index-board-list{
        background: red;
        width:100%;
    }
    /*选项元素外框*/
    .index-board-list>.index-board-item{
        width:565px;
        height:200px;
        margin-top:15px;
        float: left;
    }
    .index-board-item-evenNumber{
        margin-left: 15px;
    }

    /*选项元素背景图片*/
    .index-board-list> .index-board-earth{
        background: url("../assets/img/earth.jpg") no-repeat;
        background-size: 100% 100%;
    }
    .index-board-list> .index-board-loud{
        background: url("https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1539771025568&di=d746da7e5f1cce708110c7bcc5e2ff9a&imgtype=0&src=http%3A%2F%2Fpic.58pic.com%2F58pic%2F11%2F30%2F27%2F36u58PICpgE.jpg") no-repeat;
        background-size: 100% 100%;
    }
    .index-board-list> .index-board-car{
        background: url("../assets/img/car.jpg") no-repeat;
        background-size: 100% 100%;
    }
    .index-board-list> .index-board-hill{
        background: url("http://img2.imgtn.bdimg.com/it/u=2384301809,972507771&fm=200&gp=0.jpg") no-repeat;
        background-size: 100% 100%;
    }

    .index-board-list>.index-board-item>h2{
        margin:40px 0 0 260px;
    }
    .index-board-list>.index-board-item>p{
        margin:20px 0 0 260px;
    }
    /*立即购买 按钮*/
    .index-board-list>.index-board-item>.list-button{
        width:100px;
        height:35px;
        background: #4cb18e;
        line-height:35px;
        text-align: center;
        margin:20px 0 0 260px;
    }



</style>



